.result-list-root {
  max-width: 600px;
  margin: 48px auto;
  padding: 32px 24px;
  background: #fff;
  border-radius: 14px;
  box-shadow: 0 4px 24px #0002;
  font-family: 'Roboto Condensed', Arial, sans-serif;
}

.result-list-title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 32px;
  color: #222;
  letter-spacing: 0.01em;
}

.result-list-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  background: #f6f8fa;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 1px 4px #0001;
  font-size: 1.08rem;
}

.result-list-table th,
.result-list-table td {
  padding: 14px 16px;
  text-align: left;
}

.result-list-table th {
  background: #e5e9ef;
  color: #444;
  font-weight: 700;
  border-bottom: 2px solid #cfd8dc;
  letter-spacing: 0.02em;
}

.result-list-row {
  cursor: pointer;
  transition: background 0.15s, color 0.15s;
  outline: none;
}

.result-list-row:hover,
.result-list-row:focus {
  background: #e3f2fd;
}

.result-list-link {
  color: #0074d9;
  text-decoration: underline;
  font-size: 1.08rem;
  font-weight: 500;
  letter-spacing: 0.01em;
}

.result-list-error,
.result-list-loading {
  padding: 32px;
  font-size: 24px;
  text-align: center;
  color: #b71c1c;
}
