.problem-item-root {
    display: flex;
    height: 100vh;
    font-family: sans-serif;
    width: 100vw;
    overflow: hidden;
}

.problem-item-list-pane {
    min-width: 200px;
    max-width: 80vw;
    overflow: auto;
    border-right: 1px solid #ccc;
    background: #fafbfc;
    user-select: auto;
    transition: background 0.2s;
}

.problem-item-list-pane.resizing {
    user-select: none;
}

.problem-item-list-title-row {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 16px;
    font-weight: bold;
    font-family: 'Roboto Condensed', Arial, sans-serif;
    font-size: 18px;
    letter-spacing: 0.01em;
}

.problem-item-list-title-text {
    flex: 1;
}

.problem-item-back-btn {
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
    padding: 0 8px 0 0;
    color: #444;
}

.problem-item-download-btn {
    background: #e5e9ef;
    border: 1px solid #cfd8dc;
    border-radius: 4px;
    padding: 2px 10px;
    font-size: 14px;
    cursor: pointer;
    margin-left: 4px;
}

.problem-item-table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    font-size: 15px;
    font-weight: 350;
    font-family: 'Roboto Condensed', 'Source Han Sans', Arial, sans-serif;
    background: #f6f8fa;
    box-shadow: 0 2px 8px #0001;
    border-radius: 8px;
    margin-bottom: 16px;
    overflow: hidden;
}

.problem-item-table thead tr {
    background: #e5e9ef;
}

.problem-item-table th,
.problem-item-table td {
    padding: 8px 10px;
    text-align: left;
    border-bottom: 1px solid #e0e4ea;
}

.problem-item-table th {
    font-weight: 700;
    letter-spacing: 0.02em;
    color: #444;
    background: #e5e9ef;
    border-bottom: 2px solid #cfd8dc;
}

.problem-item-table tbody tr:last-child td {
    border-bottom: none;
}

.problem-item-table-row {
    cursor: pointer;
    transition: background 0.15s, color 0.15s;
}

.problem-item-table-row.selected {
    background: #cbeafe;
}

.problem-item-table-row:hover:not(.selected):not(.disabled) {
    background: #e3f2fd;
}

.problem-item-table-row.disabled {
    color: #aaa;
    opacity: 0.5;
}

.problem-item-table th.point-col,
.problem-item-table td.point-col,
.problem-item-table th.offset-col,
.problem-item-table td.offset-col {
    font-family: 'Lekton', 'Roboto Mono', monospace;
    letter-spacing: 0.01em;
}

.problem-item-splitter {
    width: 6px;
    cursor: col-resize;
    background: #eee;
    z-index: 10;
    user-select: none;
    transition: background 0.2s;
}

.problem-item-splitter.resizing {
    background: #b3d4fc;
}

.problem-item-preview-pane {
    flex: 1;
    min-width: 0;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: stretch;
    background: #fff;
    height: 100vh;
    position: relative;
}

.problem-item-preview-topbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 24px;
    border-bottom: 1px solid #eee;
    background: #fafbfc;
    font-size: 16px;
    min-height: 40px;
    height: 40px;
}

.problem-item-preview-zoom {
    color: #888;
}

.problem-item-preview-canvas-container {
    flex: 1;
    width: 100%;
    height: 100%;
    position: relative;
}

.problem-item-preview-canvas {
    border: 1px solid #ccc;
    background: #f8f8f8;
    cursor: grab;
    box-shadow: 0 2px 8px #0001;
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
    display: block;
}